  <div class="retireSave">
    <div class="tabButton">
      <aia-button
        :class="checkButton=='A'?'aiaButton':'uncheck'"
        type="danger"
        @click="switchButton('A')"
      >แบบ</aia-button>
      <aia-button
        :class="checkButton=='B'?'aiaButton':'uncheck'"
        type="danger"
        @click="switchButton('B')"
      >ยิ่งขึ้น</aia-button>
    </div>

    <div>
      <div class="baseMain" v-if="checkButton=='A'">
        <p>
          <span>1. คุณอยากเตรียมเงินไว้ให้คนข้างหลังเท่าไร(All Preparation)</span>
          <aia-input type="text" v-model="AllPreparation"></aia-input>
