        <p>
          <span>2. คุณเตรียมไว้แล้วเท่าไร(Existing Preparation)</span>
          <aia-input type="text" v-model="ExistingPreparation"></aia-input>
        </p>
        <p>
          <span>3. คุณต้องเตรียมเพิ่มอีก(Add Preparation)</span>
          <aia-input type="text" disabled v-model="AddPreparation"></aia-input>
        </p>
      </div>
    </div>
    <div v-if="checkButton=='B'">
      <div class="baseMainB">
        <div>
          <span>1. คุณอยากเตรียมเงินไว้ให้คนข้างหลังเท่าไร</span>
          <div>
            <mt-range v-model="CurrentAge"></mt-range>
            <aia-input style="width:1rem;margin-right:0.2rem" type="text" v-model="CurrentAge"></aia-input>
            <span>ปี</span>
          </div>
        </div>
        <div>
          <span>2. อายุเกษียณ</span>
          <div>
            <mt-range v-model="RetirementAge"></mt-range>
            <aia-input style="width:1rem;margin-right:0.2rem" type="text" v-model="RetirementAge"></aia-input>
            <span>ปี</span>
          </div>
        </div>
        <div>
          <span>3. อายุขัย</span>
          <div>
            <mt-range v-model="Lifespan"></mt-range>
            <aia-input style="width:1rem;margin-right:0.2rem" type="text" v-model="Lifespan"></aia-input>
            <span>ปี</span>
          </div>
        </div>

        <div>
          <span>4. เงินใช้จ่ายหลังเกษียณต่อเดือน</span>
          <div>
            <aia-input type="text" v-model="MoneyforLifespan"></aia-input>
          </div>
        </div>
        <div>
          <span>5. เงินก้อนที่คุณต้องเตรียมเพื่อเกษียณ</span>
          <div>
            <aia-input type="text" disabled></aia-input>
