<script>
import { mapState, mapGetters } from "vuex";

export default {
  name: "RetireSave",
  data() {
    return {
      checkButton: "B",
      value: "",
      CurrentAge: 10,
      RetirementAge: 20,
      Lifespan: 30,
      MoneyforLifespan: "",
      AllPreparation: "",
      ExistingPreparation: ""
      // AddPreparation: ""
      // SumMoneyForLifeSpan: ""
    };
  },
  created() {
    // console.log(this.$store);
    this.getAge();
  },
  computed: {
    SumMoneyForLifeSpan() {},
    AddPreparation() {
      if (this.AllPreparation - this.ExistingPreparation <= 0) {
        return "0.00";
      } else {
        return this.AllPreparation - this.ExistingPreparation;
      }
    },
    ...mapState({
      birthDate: state => state.customerInfo.birthDate
    })
  },
  methods: {
    switchButton(res) {
      this.checkButton = res;
    },
    // getAge() {
    //   let BD = this.birthDate.split("/")[2];

    //   let nowDate = new Date().getFullYear();
    //   let year = nowDate - BD;
    //   // console.log(nowDate);
    //   // conso/le.log(year);
    //   this.CurrentAge = year;
    // },
    saveRetireDataToOC() {
      let _this = this;

      WebViewJavascriptBridge.callHandler(
        "saveSavingRetirement",
        {
          value: _this.value,
          CurrentAge: _this.CurrentAge,
          RetirementAge: _this.RetirementAge,
          Lifespan: _this.Lifespan,
          MoneyforLifespan: _this.MoneyforLifespan,
          AllPreparation: _this.AllPreparation,
          ExistingPreparation: _this.ExistingPreparation
        },
        function(response) {
          alert(response);
        }
      );
    },
    getAge() {
      let strAge = this.birthDate;
      var birArr = strAge.split("/").reverse();
      var birYear = birArr[0];
      var birMonth = birArr[1];
      var birDay = birArr[2];

      d = new Date();
      var nowYear = d.getFullYear();
      var nowMonth = d.getMonth() + 1; //记得加1
      var nowDay = d.getDate();
      var returnAge;

      if (birArr == null) {
        return false;
      }
      var d = new Date(birYear, birMonth - 1, birDay);
      if (
        d.getFullYear() == birYear &&
        d.getMonth() + 1 == birMonth &&
        d.getDate() == birDay
      ) {
        if (nowYear == birYear) {
          returnAge = 0; //
        } else {
          var ageDiff = nowYear - birYear; //
          if (ageDiff > 0) {
            if (nowMonth == birMonth) {
              var dayDiff = nowDay - birDay; //
              if (dayDiff < 0) {
                returnAge = ageDiff - 1;
              } else {
                returnAge = ageDiff;
              }
            } else {
              var monthDiff = nowMonth - birMonth; //
              if (monthDiff < 0) {
                returnAge = ageDiff - 1;
              } else {
                returnAge = ageDiff;
              }
            }
          } else {
            return "出生日期晚于今天，数据有误"; //返回-1 表示出生日期输入错误 晚于今天
          }
        }
        this.CurrentAge = returnAge;
        // return returnAge;
      } else {
        return "输入的日期格式错误！";
      }
    }
  }
};
</script>

