<template>
  <div class="otherSave">
    <div class="otherLeft" style="width:50%">
      <p>{{futureValue*1>FV*1?"Success":"fail"}}</p>
      {{futureValue}}
      <el-form ref="ruleForm" label-width="2rem" class="demo-ruleForm">
        <el-form-item label="Saving For">
          <aia-select placeholder="请选择" v-model="selectValue" style="width:100%">
            <aia-option
              v-for="item in SavingFor"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></aia-option>
          </aia-select>
        </el-form-item>
        <el-form-item label="All Preparation(FV)">
          <el-input v-model="FV"></el-input>
        </el-form-item>
        <el-form-item label="Preparation Year(NPER)">
          <el-input v-model="NPER"></el-input>
        </el-form-item>
        <el-form-item label="Existing Preparation(PV)">
          <el-input v-model="PV"></el-input>
        </el-form-item>
        <el-form-item label="Prepare per Month(PMT)">
          <el-input v-model="PMT"></el-input>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { fv, pv, pmt } from "../../utils/baseFun";

export default {
  name: "OtherSave",
  data() {
    return {
      SavingFor: [
        {
          value: "Government",
          label: "Government sector/Stat enterprise"
        }
      ],
      selectValue: "Government",
      fvdata: 0,
      FV: 0,
      PV: 0,
      NPER: 0,
      PMT: 0
    };
  },
  computed: {
    futureValue() {
      return fv(0.015, this.NPER, -this.PMT, this.PV);
      // return fv(0.015, 1, -10000, 1000000);
    }
  },
  created() {
    this.fvdata = fv(0.015, 1, -10000, 1000000);
  },
  methods: {}
};
</script>

