function pv(rate, nper, pmt, fv) {
    var result = 0;

    let rates = rate / 12;
    let npers = nper * 12;
    result =
        (fv + (pmt / rates) * (Math.pow(1 + rates, npers) - 1)) /
        Math.pow(1 + rates, npers);
    // this.pvdata = result;
    return result.toFixed(2)

}

function fv(rate, nper, pmt, pv) {
    /*rate  利率
      nper  年
   pmt  还款
   pv 本金
  **/
    let rates = rate / 12;
    let npers = nper * 12;

    var result = 0;
    result =
        pv * Math.pow(1 + rates, npers) -
        (pmt / rates) * (Math.pow(1 + rates, npers) - 1);

    return result.toFixed(2);
}

function pmt(rate, nper, pv, fv) {
    var result = 0;
    let rates = rate / 12;
    let npers = nper * 12;
    result =
        (pv * rates * Math.pow(1 + rates, npers) - fv * rates) /
        (Math.pow(1 + rates, npers) - 1);
    // this.pmtData = result;
    return result.toFixed(2);

}
export {
    pv,
    fv,
    pmt
}